const LanguageSwitcher = forwardRef((props, ref) => {
 
  const {locale,L,setLanguage,setStrings,getString} = useLocale();
  const {data,setData} = useNamespace();
  const {getUrlArgs} = useUrl(urlOptions);
   
   const handleClick=(language)=>{
       setLanguage(language);
       if (!data[language]) // load only if is not loaded already
       if(typeof props.onSwitch=='function'){
           props.onSwitch({
            k:getUrlArgs('k'),
               l:language
             });
       }
   };

     return ( 
       <Dropdown drop={'bottom'} className={"language-switcher show " +( props.className||"" )}> 
      
      <Dropdown.Toggle  className={'language-switcher-toggle btn-dark dropdown-menu-end '}    >
           
      {/* <i className={`fi fi-${locale.current} me-2`}></i>  */}
      <i className={`fi fi-${locale.current} `}></i> 
           {/* {locale.current} */}
       </Dropdown.Toggle> 
       <Dropdown.Menu >
       {locale.available.map((language, index) => ( 
           <Dropdown.Item key={index} onClick={()=>{handleClick(language)}}  className={(locale.current==language) ? ' active' : ''} >
       
       {/* <i className={`fi fi-${language} me-2` }></i>  */}
       <i className={`fi fi-${language} me-2` }></i> 
           {/* {language}  */}
           {L(language+'LangName')} 
           
           </Dropdown.Item>
       ))} 
           
       </Dropdown.Menu>

       </Dropdown>
     );
    
 });

