 // universal data fetcher 
  const fetchData=(requestData)=>{
    
    httpRequest(process.env.REACT_APP_API_URL+'/release/data', requestData, 'POST')  .then(response => {  
           if(!response.error){
           setData({...data,...response,...{args:urlArgs}});
         }else{
         
           setData({...response,...{args:urlArgs}});
         } 
       })
       .catch(error => { 
      
           let response = {error:1,exception:(typeof error ==='object') ? JSON.stringify(error):error} 
           setData({...response,...{args:urlArgs}});  
         
       });

     
   
   }

// data string picker
const D=(key)=>{
 let res;
 if(data)
   if(locale)
     if(locale.current){
       res = oget(data[locale.current],key);
     if(res){
           return res; // return language data string
         }else{
             res = oget(data['common'],key);
           if(res){
             return res; // return common data string
           }else{
           return `${locale.current}.${key}`; // if not set return warning 
         }
         }
     }

 return '';// return empty if not loaded yet
 
}

