const Text = ({ children, limit, after }) => {
  const limitChars = (text, limit) => {
    if (!text || !limit || limit === 0) return text;
    if (limit > text.length) return text;

    for (let i = limit - 1; i >= 0; i--) {
      if (text[i] === ' ' || text[i] === ',' || text[i] === '.'|| text[i] === ')') {
        // return text.substring(0, i) + (text.length > i ? after : '');
        return text.substring(0, i) ;
      }
    }

    // no separator situation
    return text.substring(0, limit);
  };

  const [limitedText, setLimitedText] = useState('');

  useEffect(() => {
    setLimitedText(limitChars(children, limit));
  }, [children, limit, after]);

  return <>
  {(!limit || (!children ) || (limit && (limit>=children.length || limit==0) )) && <>{children}</>}
{(limit && children && limit<children.length   ) && 
   <>
   {limitedText} {(limitedText.length<children.length && after) && after}
   </>}
</>
};

  const Box = (props)=>{  
  const textLimit = props.textLimit;
  const {index,title, text,link,type,group,tags} = props.item;
    return (  
      <div  key={index}  className={'box '+(props.className||'')}>   
        <div    className={'card'}> 
<div className="card-header">
    <h5 onClick={()=>{toElement(index)}}className="inline card-title mb-0"> {title} </h5>   
  
    <div className=""> 
    <Breadcrumbs {...{type,group}}/>  
     </div>
  </div>
  <div className="card-body"> 
    <p className="card-text"><Text  limit={textLimit} after={ <a className="card-link" onClick={()=>{toElement(index)}}> [...]</a>  }>{text}</Text></p>   
    {/* {link &&  <a href={link} target='_blank' className="card-link">{L('Source')}</a>   } */}
  </div>

  {tags &&   <div className="card-footer">    <TagButtons tags={tags}/> </div>} 
  </div> 
  </div>
   
    );
    };



    const Showcase=({title,filter,textLimit,boxClassName})=>{
  
 
      return(<div className=" container pt-4">
      <div className="section-title">
       <p>{title}</p>
      </div>
      <div className="row">
     
      {data[locale.current].elements
    .map((item, index) => ({ ...item, index: index }))  // Add the original index to each item
    .filter(filter)  // Filter the items
    .map((item) => (
    
    //  <KnowledgesItem key={item.index} item={item} />
    //  <Box key={item.index} item={item} textLimit={textLimit} className="col-12  mt-2 "/>
     <Box key={item.index} item={item} textLimit={textLimit} className={boxClassName} />
    ))}
     
     
         
      </div>
     </div>);
     }
     
     
    const Related = ({  boxClassName, title, textLimit }) => {
      const [randomItemIds, setRandomItemIds] = useState([]);
      function generateRandomItemIds(count, min, max) {
        const allNumbers = Array.from({ length: max - min + 1 }, (_, i) => i + min);
        const randomNumbers = [];
        while (randomNumbers.length < count) {
            const randomIndex = Math.floor(Math.random() * allNumbers.length);
            const randomNumber = allNumbers.splice(randomIndex, 1)[0];
            randomNumbers.push(randomNumber);
        }
        return randomNumbers;
    }
      useEffect(() => {
     
       
       
        setRandomItemIds(generateRandomItemIds(12, 0, data[locale.current].elements.length-1) );
     
        
      }, []);
    
      return (
        <Showcase filter={(item,index)=>{return  randomItemIds.includes(index)}} boxClassName={boxClassName} title={title} textLimit={textLimit} />
      );
    };

// single element viewer 
const Element = forwardRef((props,ref)=>{


  const [item,setItem]=useState({})
  
 
  const ElementModel =({item})=>{
   const {title, place,date,text, more,type,index,link,tags,group}=item;
   return(
   
   <>
   <button  className="btn btn-sm btn-back" onClick={()=>{window.navigateToSection('resume')}}  >
   <i className='=bi bi-x '></i>
  </button>
  <div className="container">
  
   <div className="section-title"> 
    <p>{title}</p> 
   </div>
   <div className="row">
   <Breadcrumbs {...{type,group}}/>  
  {tags &&      <TagButtons tags={tags}/> } 
   <div className="">
   {/* <h4>{title}</h4> */}
    {date &&   <h5>{date}</h5>}
{ place &&    <p> <em>{place}</em> </p>}
{text && <p> {text} </p>}
{link && <a href={link}> {link} </a>}
   
   {more && (
   <div className="">
   
    <ul>
      {more.map((e, i) => (
        <li key={i}>
          <p>{e.title}</p>
          <p>{e.text}</p>
        </li>
      ))}
    </ul>
   
   </div>
   )}
   
   </div>
   
   </div>
   
   </div>
   </>);
 }
  
  
  
    function update(index){
     
      const itemData = data[locale.current].elements[index];
 
      if(itemData){
       
        setItem(itemData)
  
      }else{
        // todo navigate to error 
       console.error('Error: Element ['+index+'] is invalid')
   
      } 
    } 
  useExpose(ref,{ 
    update
  });
    useEffect(() => {
 
  }, []);
 
  
  // ref = useRef(ref);
 return( 
 <>{
 
 data &&   <section  ref={ref} id="element" className="element">
 
 <ElementModel item={item}/> 
 

 <Related boxClassName="col-12 col-md-6 col-lg-4 mb-2" title={L('Related')}   textLimit={70}/>

 </section>
 }</>
 );
 });
 
 




