import React, { useState, useEffect ,useContext} from 'react';
  
 
// import  Router from '@iorp/react-aid/lib/components/Router' ; 


import  Namespace from '@iorp/react-aid/lib/components/Namespace' ;
import Locale from '@iorp/react-aid/lib/components/Locale';
  


 import routes from './routes'
import {localeOptions, namespaceOptions } from './extensions/release/config';
const NotFound = (props) => (
  <div>
    <h1>404 - Not Found</h1>
    <p>path: {props.currentPath}</p>
    <p>base: {props.basename}</p>
    <p>Sorry, the page you are looking for does not exist.</p>
  </div>
); 

 
const Router = ({ basename, routes }) => {
  const [currentPath, setCurrentPath] = useState(getCurrentPath());

  useEffect(() => {
    const handlePopstate = () => {
      setCurrentPath(getCurrentPath());
    };

    window.addEventListener('popstate', handlePopstate);

    return () => {
      window.removeEventListener('popstate', handlePopstate);
    };
  }, []);

  function getCurrentPath() {
    const pathname = window.location.pathname;
    let trimmedPath = pathname.startsWith(basename) ? pathname.slice(basename.length) : pathname;

    trimmedPath = trimmedPath || '/';
    trimmedPath = trimmedPath.startsWith('/') ? trimmedPath : '/' + trimmedPath;
    return trimmedPath;
  }

  // Automatically remove last / if it ends with it
  if (currentPath.length > 1 && currentPath.endsWith('/')) {
    setCurrentPath(currentPath.substring(0, currentPath.length - 1));
  }

  const currentRoute = routes.find(route => route.path === currentPath);
  const ElementToRender = currentRoute ? currentRoute.element : NotFound;

  return (
    <div>
      <ElementToRender basename={basename} currentPath={currentPath} />
    </div>
  );
};

// Function to navigate programmatically
export function navigateTo(path) {
  window.history.pushState({}, '', path);
  // Manually trigger the popstate event
  window.dispatchEvent(new PopStateEvent('popstate'));
}
const App = ()=>{ 
  
  return  <Namespace options={namespaceOptions}> 
      <Locale options={localeOptions}>
   {/* //process.env.PUBLIC_URL */}
    <Router basename={process.env.REACT_APP_BASENAME} routes={routes}/>  
    </Locale>
    </Namespace>
}  

 
export default App;